$red_text: red;
$blue_text: blue;

@mixin box1 {
    width: 250px;
    height: 200px;
    background-color: green;
    display: block; }

@mixin  box2 {
    width: 250px;
    height: 200px;
    background-color: red;
    display: block; }

h1 {
    color: white;
    font-size: 50px;
    @include box2; }
h3 {
    color: blue; }

.block {
    background-color: #000;

    h3 {
        color: white; }

    &_color {
        height: 100px;
        width: 100%;
        background-color: #f0f;
        button {
            @include box1; } }
    .block_color > h3 {
        color: $red_text !important;
        font-size: 5rem; } }
